.verticle-line{
    position: fixed;
    top: 0;
    height: 100%;
    width: 3px;
    background-color: rgb(86, 86, 86);
    margin-left: 5%;
    padding-top: 150px;
}

.full-side-bar{
    margin: 0 20px;
}

.side-bar-large, .side-bar{
    display: flex;
    margin-bottom: 10px;
    margin-top: 10px;
    width: 200px;
    align-items: start;
}

.side-bar-small-title{
    font-size: 20px;
    margin-bottom: 0px;
    margin-top: 0px;
}

.side-bar-large{
    margin-bottom: 50px;
}

.side-bar-large-title{
    font-size: 55px;
    margin-bottom: 0px;
    margin-top: 0px;
}

.side-bar-medium-title{
    font-size: 25px;
    margin-bottom: 0px;
    margin-top: 0px;
    width: 500px;
}

.side-bar-chinese{
    display: flex;
    gap: 2%;
}

.side-bar-chinese .side-bar-name{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.history-main-content{
    margin: 300px 10% 0px max(250px, 22%);
    gap: 20%;
    /* background-color: rgba(90, 90, 90, 0.5); */
    overflow: clip;
    align-items: center;
}

.history-main-img{
    position: fixed;
    top: 300px;
    width: 35%;
}

.history-main-img img{
    width: 100%;
    
}

.history-scrolling-text{
    flex-basis: 30%;
    margin: 0px 0px 0px max(300px, 60%);
}

.history-scrolling-text .chunk{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.history-main-img.show{
    display: block;
}

