.time-line{
    background-color: rgb(255, 255, 255);
    padding: 8% 0% 5% 0%;
}
.line{
    height: 10px;
    background-color: rgba(0, 0, 0, 0.603);
    
}

.cards{
    display: flex;
    gap: 5%;
    justify-content: space-between;
    margin: 5% 5%;
    flex-wrap: wrap;
}

.card-img{
    width: 100%;
    height: 30%;
    object-fit: cover;
    margin-top: 5%
}

.time-period-card{
    background-color: rgb(210, 210, 210);
    padding: 1% 2%;
    box-sizing: border-box;
    flex: 0 1 auto;
    height: 30vw;
    width: 10%;
    border-radius: 4px;
}

.time-period-card:hover{
    background-color: rgba(90, 90, 90, 0.681);
}

.time-period-card a{
    color: #000000;
}

.time-period-card a:hover{
    color: #ffffff;
}

.time-period-card h1{
    font-size: 1vw;
    font-weight: 100;
    text-align: left;
    height: 50px;
}

.time-period-card img{
    width: 5vw;
    height: 30%;
    object-fit: cover;
    margin: 2% 0;
}

.time-period-card h2{
    font-size: 3vw;
    font-weight: 100;
    text-align: left;
    margin: -1% 0px;
}

.time-period-card p{
    font-size: 1.5vw;
    font-weight: 100;
    text-align: left;
    font-weight: 100;
    margin: -1% 0px;
}

.time-period-card .chinese-name, .time-period-card .pinyin-name{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}


.time-period-card .phase-name{
    display:flex;
    gap: 5%;
    margin: 30% auto;
    justify-content: center;
    flex: 1 1 auto;
}